<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班干部设置</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="$router.push({path:'/setCadres'})">管理</span>
        </div>
        <div class="content f28 c333" style="padding:0.84rem 0 1.7rem;">
            <ul class="pl-30">
                <li class="b_line pr-30" v-for="(item,index) in list" :key="index">
                    <img :src="item.img">
                    <span>{{item.name}}</span>
                    <span class="onDuty f20 cfff ml-20" :style="{display:item.type!=2?'block':'none'}">班干部</span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            list:[
                {
                    type:1,
                    name:'张三丰',
                    img:require('../../assets/message/onShow.png'),
                },{
                    type:3,
                    name:'金毛狮王',
                    img:require('../../assets/message/onShow.png'),
                },{
                    type:2,
                    name:'金轮法王',
                    img:require('../../assets/message/onShow.png'),
                },{
                    type:2,
                    name:'杨过',
                    img:require('../../assets/message/onShow.png'),
                },{
                    type:2,
                    name:'小龙女',
                    img:require('../../assets/message/onShow.png'),
                },{
                    type:2,
                    name:'金毛狮王',
                    img:require('../../assets/message/onShow.png'),
                },
            ]
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
     .loginNav{
        position: fixed;
    }
    
    .content{
        line-height: 1rem;
    }
    .content img{
        width:.6rem;
        height:.6rem;
        border-radius: 50%;
        margin-right: .3rem;
    }
    li{
        display: flex;
        align-items: center;
    }
    .onDuty{
        width:.7rem;
        height:.28rem;
        background:#7C88EE;
        line-height: .28rem;
        display: inline-block;
        text-align: center;
        border-radius: .05rem;
    }
</style>




